<template>
  <div class="wrap">
    <!-- banner -->
    <div class="swipe-box" v-if="banners.length>0">
      <div class="swipe-container">
        <van-swipe :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="banner in banners" :key="banner.adId">
            <img :src="banner.img" />
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <!--  -->
    <div class="title-wrap">
      <div class="title"></div>
    </div>
    <van-grid :column-num="2" :gutter="10">
      <van-grid-item :to="'/goods?prodId='+item.prodId" v-for="item in productList" :key="item.prodId">
        <van-image :src="item.img" />
        <div class="goodsItem">
          <p class="name">{{item.prodName}}</p>
          <div class="price">{{$util.priceAndPointsShow(item.unitPrice,item.unitPoints)}}</div>
        </div>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import {
  Swipe,
  Toast,
  SwipeItem,
  Grid,
  GridItem,
  Image,
  NavBar,
  Icon
} from "vant";
import { comData } from "@/mix/comData";
import gql from "graphql-tag";
export default {
  name: "index",
  mixins: [comData],
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Toast.name]: Toast,
    [Grid.name]: Grid,
    [GridItem.name]: GridItem,
    [Image.name]: Image,
    [NavBar.name]: NavBar,
    [Icon.name]: Icon
  },
  data() {
    return {
      banners: [],
      mallInfo: {},
      productList:[],
      total:0
    };
  },
  mounted() {
    this.queryMallInfo();
  },
  methods: {
    onClickLeft() {
      Toast("");
    },

    onClickRight() {
      Toast("");
    },

    onClickCart() {
      this.$router.push("cart");
    },

    onClickUser() {
      this.$router.push("user");
    },

    async queryMallInfo() {
      const query = gql`
        query indexMallInfo(
          $mallId: String!
          $position: String!
          $skip:Int
          $limit:Int
        ) {
          mall {
            ad {
              list(position: $position, mallId: $mallId) {
                adId
                href
                img
                title
              }
            }
            product{
              list(mallId:$mallId,skip:$skip,limit:$limit){
                prodId
                prodImg
                prodName
                sales
                sortId
                stock
                unitPrice
                unitPoints
              }
              total(mallId:$mallId)
            }
          }
        }
      `;
      const result = await this.$apollo.query({
          query: query,
          variables: {
            mallId: this.mallId,
            position: "banner",
            skip:0,
            limit:10
          }
        })
       let {
          data: {
            mall: {
              ad,product
            }
          }
        } = result
        this.banners = ad.list
        this.productList = product.list
        this.total = product.total
        console.log(ad,product)
    }
  }
};
</script>

